<template>
  <el-dialog
    :visible="visible"
    append-to-body
    title=""
    :close-on-click-modal="false"
    @update:visible="$emit('change', $event)"
  >
    <el-alert
      title=""
      type="warning"
      show-icon
      style="margin-bottom: 20px"
    ></el-alert>
    <prism-editor
      class="f-render-code"
      v-model="code"
      :highlight="highlighter"
      line-numbers
    ></prism-editor>
    <div style="text-align: center;margin-top: 20px">
      <el-button @click="handleChangeCode" type="primary"></el-button>
      <el-button @click="handleCopyData" type="primary"></el-button>
      <el-button type="primary"
        ><el-link
          :href="fileURL"
          style="color: white;"
          :underline="false"
          :download="`${Date.now()}.js`"
          ></el-link
        ></el-button
      >
      <el-button @click="$emit('change', false)"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";

import _ from "lodash";
import copy from "clipboard-copy";
import { reomveQuotes, objectToArr } from "../../../utils";

export default {
  inject: ["frender"],
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      code: "",
      fileURL: ""
    };
  },
  watch: {
    visible(val) {
      if (val) {
        this.code = reomveQuotes(this.frender.getFormConfigStr());
      }
    },
    code: {
      handler() {
        // 
        if (!this.codeToURL) {
          this.codeToURL = _.debounce(() => {
            //  URL
            const blob = new Blob([this.code]);
            this.fileURL = URL.createObjectURL(blob);
          }, 300);
        }
        this.codeToURL();
      },
      immediate: true
    }
  },
  methods: {
    // 
    highlighter(code) {
      return highlight(code, languages.js);
    },
    // 
    handleChangeCode() {
      try {
        // 
        const res = eval("(" + this.code + ")");
        // 
        if (_.isPlainObject(res)) {
          // 1.  formDesc  formPropsData
          const { formDesc = {}, order = [], ...formPropsData } = res;
          // 2.  frender.formPropsData
          this.frender.formPropsData = Object.assign(formPropsData, {
            order: order || Object.keys(formDesc)
          });
          // 3.  formDesc  frender.formItemList
          this.frender.formItemList = objectToArr(formDesc, "field").map(item =>
            Object.assign({ attrs: {} }, item)
          );

          // 
          this.$emit("change");
        } else {
          // 
          throw new TypeError(res + ", ");
        }
      } catch (err) {
        // 
        this.$message.error(" JS ");
        console.error(err);
      }
    },
    // 
    handleCopyData() {
      copy(this.code);
      this.$message.success("!");
    }
  }
};
</script>
