<template>
  <el-dialog
    :visible="visible"
    append-to-body
    title=""
    @update:visible="$emit('change', $event)"
  >
    <el-radio-group v-model="showType">
      <el-radio label="f-render">f-render</el-radio>
      <el-radio label="vue-ele-form">vue-ele-form</el-radio>
    </el-radio-group>
    <prism-editor
      class="f-render-code"
      :value="code"
      :highlight="highlighter"
      line-numbers
      readonly
    ></prism-editor>
    <div style="text-align: center;margin-top: 20px">
      <el-button @click="handleCopyCode" type="primary"></el-button>
      <!--  TODO  -->
      <el-button type="primary"
        ><el-link
          :href="fileURL"
          style="color: white;"
          :underline="false"
          :download="`${Date.now()}.vue`"
          ></el-link
        ></el-button
      >
      <el-button @click="$emit('change', false)"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import copy from "clipboard-copy";

import { PrismEditor } from "vue-prism-editor";
import { highlight, languages } from "prismjs/components/prism-core";

import FRenderTpl from "./tpl/f-render";
import EleFormTpl from "./tpl/vue-ele-form";

import _ from "lodash";
const indentString = require("indent-string");
import { reomveQuotes } from "../../../utils";

export default {
  inject: ["frender"],
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  components: {
    PrismEditor
  },
  data() {
    return {
      code: "",
      fileURL: "",
      showType: "f-render"
    };
  },
  watch: {
    //  showType 
    showType() {
      this.setCode();
    },
    //  visible 
    visible(val) {
      if (val) {
        this.setCode();
      }
    },
    //  code  URL
    code() {
      // 
      if (!this.codeToURL) {
        this.codeToURL = _.debounce(() => {
          //  URL
          const blob = new Blob([this.code]);
          this.fileURL = URL.createObjectURL(blob);
        }, 200);
      }

      this.codeToURL();
    }
  },
  methods: {
    // 
    setCode() {
      const tpl = this.showType === "f-render" ? FRenderTpl : EleFormTpl;
      this.code = tpl(
        indentString(reomveQuotes(this.frender.getFormConfigStr()), 6).trim()
      );
    },
    // 
    highlighter(code) {
      return highlight(code, languages.js);
    },
    // 
    handleCopyCode() {
      copy(this.code);
      this.$message.success("!");
    }
  }
};
</script>
