<template>
  <ele-form-dialog
    v-model="formData"
    :formDesc="formDesc"
    labelPosition="left"
    width="800px"
    :dialogAttrs="{
      'close-on-click-modal': false,
      'append-to-body': true
    }"
    @request="handleAdd"
    :visible="visible"
    @update:visible="$emit('change')"
    title=""
  ></ele-form-dialog>
</template>

<script>
import _ from "lodash";

export default {
  inject: ["frender"],
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    visible(val) {
      if (val) {
        this.formData = {
          type: "dynamic",
          formItems: []
        };
      }
    }
  },
  computed: {
    // 
    fieldCountObj() {
      return _.countBy(this.frender.formItemList, o => String(o.field).trim());
    },
    formDesc() {
      return {
        type: {
          type: "radio",
          label: "",
          default: "dynamic",
          options: [
            { text: "", value: "dynamic" },
            {
              text: "()",
              value: "mysql",
              attrs: { disabled: true }
            }
          ]
        },
        formItems: {
          type: "dynamic",
          label: "",
          isTypeChangeReloadValue: false,
          attrs: {
            columns: [
              {
                type: "el-input",
                valueKey: "field",
                attrs: {
                  placeholder: " field"
                }
              },
              {
                type: "el-input",
                valueKey: "label",
                attrs: {
                  placeholder: " label"
                }
              },
              {
                type: "el-select",
                valueKey: "type",
                slots: {
                  default: h => {
                    return this.frender.sortedComps.map(item =>
                      h("el-option", {
                        attrs: {
                          label: `${item.type} - ${item.label}`,
                          value: item.type
                        }
                      })
                    );
                  }
                },
                attrs: {
                  filterable: true,
                  placeholder: " type"
                }
              }
            ]
          },
          rules: {
            type: "array",
            validator: (rule, value, callback) => {
              let errorArr = [];
              if (Array.isArray(value) && value.length) {
                // 
                const currentFieldCountObj = _.countBy(value, o =>
                  String(o.field).trim()
                );

                // 
                const mergedCountObj = Object.assign(
                  {},
                  currentFieldCountObj,
                  _.mapValues(this.fieldCountObj, (val, field) => {
                    return currentFieldCountObj[field]
                      ? val + currentFieldCountObj[field]
                      : val;
                  })
                );

                // 
                errorArr = value
                  .map((item, index) =>
                    mergedCountObj[item.field] > 1
                      ? `${index + 1} field `
                      : null
                  )
                  .filter(Boolean);

                // 
                errorArr = errorArr.concat(
                  value
                    .map((item, index) => {
                      const errMsg = [];
                      if (!item.field) errMsg.push("field");
                      if (!item.label) errMsg.push("label");
                      if (!item.type) errMsg.push("type");

                      return errMsg.length
                        ? `${index + 1} ${errMsg.join("")} `
                        : null;
                    })
                    .filter(Boolean)
                );
              }
              errorArr.length
                ? callback(new Error(errorArr.join(" & ")))
                : callback();
            }
          }
        }
      };
    }
  },
  data() {
    return {
      formData: {
        type: "dynamic",
        formItems: []
      }
    };
  },
  methods: {
    handleAdd({ formItems }) {
      try {
        const newFormItems = formItems.map(({ field, label, type }) => ({
          ...this.frender.getFormItemByType(type),
          field: String(field).trim(),
          label: String(label).trim()
        }));

        this.frender.formItemList = [
          ...this.frender.formItemList,
          ...newFormItems
        ];

        this.$message.success("");
        // 
        this.formData = {
          type: "dynamic",
          formItems: []
        };

        this.$emit("change", false);
      } catch (err) {
        console.error(err);
      }
    }
  }
};
</script>
